<template>
  <div>
    <div id="container" />
  </div>

</template>

<script>
import OrbitControls from 'three-orbitcontrols'
import * as THREE from 'three'
export default {
  name: 'ThreeDemo',
  props: {
    color: {
      type: String,
      default: 'pink'
    },
    msg: {
      type: String,
      default: 'Hello World!'
    }
  },
  data() {
    return {}
  },
  mounted() {
    this.initialize()
  },
  methods: {

    initialize() {
      /**
     * Scene
     */
      var scene = new THREE.Scene()
      /**
     * 
     */
      // var geometry = new THREE.SphereGeometry(60, 40, 40); //
      var geometry = new THREE.BoxGeometry(100, 100, 100) // Geometry
      var material = new THREE.MeshLambertMaterial({
        color: 0x0000ff
      }) // Material
      var mesh = new THREE.Mesh(geometry, material) // Mesh
      scene.add(mesh) // 

      /**
    * 
    */
      var geometry2 = new THREE.SphereGeometry(60, 40, 40)
      var material2 = new THREE.MeshLambertMaterial({
        color: 0xff00ff
      })
      var mesh2 = new THREE.Mesh(geometry2, material2) // Mesh
      mesh2.translateY(120) // Y120
      scene.add(mesh2)

      /**
     * 
     */
      // 
      var point = new THREE.PointLight(0xffffff)
      point.position.set(400, 200, 300) // 
      scene.add(point) // 
      // 
      var ambient = new THREE.AmbientLight(0x444444)
      scene.add(ambient)
      // console.log(scene)
      // console.log(scene.children)
      /**
     * 
     */
      var width = window.innerWidth // 
      var height = window.innerHeight // 
      var k = width / height // 
      var s = 200 // 
      // 
      var camera = new THREE.OrthographicCamera(-s * k, s * k, s, -s, 1, 1000)
      camera.position.set(200, 300, 200) // 
      camera.lookAt(scene.position) // ()
      /**
     * 
     */
      var renderer = new THREE.WebGLRenderer()
      renderer.setSize(width, height)// 
      renderer.setClearColor(0xb9d3ff, 1) // 
      this.$el.querySelector('#container').appendChild(
        renderer.domElement
      )
      // document.body.appendChild(renderer.domElement); //bodycanvas
      //    
      renderer.render(scene, camera)

      //  requestAnimationFrame
      // let starTime=new Date();
      // function render(){
      //   let endTime=new Date();
      //   let dance=endTime-starTime
      //   starTime=endTime
      //   requestAnimationFrame(render)
      //     renderer.render(scene, camera),
      //     mesh.rotateY(0.001*dance);
      // }
      // render()

      function render() {
        renderer.render(scene, camera)
      }

      render()
      var controls = new OrbitControls(camera, renderer.domElement)// 
      controls.addEventListener('change', render)
    }
  }
}
</script>

<style lang="scss">

</style>




